<script>
export default {
    name: 'EmojiPicker',
    props: {
        isActive: Boolean
    },
    methods: {
        changeEmoji: function(event) {
            this.$emit('add-emoji', event.target.innerText);
        }
    }
}
</script>

<style>
    .inner-wrap {
        padding-right: 7px;
    }
    .emojies-wrap {
        background: #fff;
        border-radius: 4px;
        position: absolute;
        top: -261px;
        right: 8px;
        padding: 5px;
        border: 1px solid #d7d7d7;
        border-radius: 4px;
        box-shadow: 0 0 10px #33333322;
    }
    .emojies {
        width: 266px;
        height: 255px;
        background: #fff;
        border-radius: 3px 0 0 0;
    }
    .emoji {
        user-select: none;
        cursor: pointer;
        display: inline-block;
        padding: 4px;
        height: 25px;
        width: 25px;
    }
    .vb-dragger {
        right: 2px;
        width: 6px;
        background: rgb(167, 167, 167);
        border-radius: 3px;
    }
    .triangle-wrap {
        position: relative;
    }
    .triangle-wrap::before {
        content: '';
        position: absolute;
        display: block;
        border: 10px solid;
        width: 10px;
        border-color: #d7d7d7 transparent transparent transparent;
        bottom: -25px;
        right: 8px;
    }
    .triangle-wrap::after {
        content: '';
        position: absolute;
        display: block;
        border: 10px solid;
        width: 10px;
        border-color: #fff transparent transparent transparent;
        bottom: -24px;
        right: 8px;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .2s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
        opacity: 0;
    }
</style>